import React, { ReactNode, CSSProperties, PropsWithChildren } from 'react';
import styled from 'styled-components';
import paragraphFactory from './paragraphFactory';

const TextWrapper = styled.div`
  line-height: 20px;
`;

export default function textParagraphFactory(icon?: ReactNode, style?: CSSProperties) {
  const BasicParagraph = paragraphFactory(icon, style);
  return (props: PropsWithChildren<any>) => {
    const { children } = props;
    if (!children) return null;
    return (
      <TextWrapper>
        <BasicParagraph>
          {children}
        </BasicParagraph>
      </TextWrapper>
    );
  };
}